  top: 150%;
  left: 50%;
  transform: translateX(-50%);
  transform: translateX(-50%);
  transform: translateX(-50%);
}
.tooltip--bottom::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-style: solid;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  transform: translateX(-50%);
  transform: translateX(-50%);
  border-width: 0 9px 8px;
  border-color: transparent transparent #37424a transparent;
}

.tooltip--right,
.tooltip--left {
  top: 50%;
  left: 150%;
  transform: translateY(-50%);
  transform: translateY(-50%);
  transform: translateY(-50%);
}
.tooltip--right::before,
.tooltip--left::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-style: solid;
  left: -8px;
  top: 50%;
  border-width: 9px 8px 9px 0;
  transform: translateY(-50%);
  transform: translateY(-50%);
  transform: translateY(-50%);
  border-color: transparent #37424a transparent transparent;
}