.button, .btn, #edit-reset {
  background-color: #007db3;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  line-height: normal;
  padding: 0.75em 2em;
  position: relative;
  text-align: center;
  text-decoration: none;
}

/* Main
 ========================================================================== */
.btn:hover, #edit-reset:hover, .btn:focus, #edit-reset:focus {
  background-color: #00a1e6;
  color: #fff;
  text-decoration: none;
}
.btn i, #edit-reset i {
  background: #fff;
  border-radius: 50%;
  color: #007db3;
  display: inline-block;
  font-weight: 600;
  margin-right: 0.5em;
  padding: 0.4em;
  text-align: center;
  vertical-align: middle;
}
.btn .icon-chevron-right, #edit-reset .icon-chevron-right,
.btn .icon-chevron-left,
#edit-reset .icon-chevron-left {
  background: transparent;
  border-radius: 0;
  color: #fff;
  font-size: 150%;
  line-height: 0.4;
  padding: 0;
  vertical-align: sub;
}

