  background-color: #007db3;
  color: #fff;
  padding: 0.4em;
}
.btn--outline i.icon-chevron-left,
.btn--outline i.icon-chevron-right {
  color: #007db3;
}
.btn--outline:hover i.icon-chevron-left,
.btn--outline:hover i.icon-chevron-right, .btn--outline:focus i.icon-chevron-left,
