/* Mobile Menu Button */
:root {
  --bar-width: 20px;
  --bar-height: 2px;
  --hamburger-gap: 6px;
  --foreground: white;
  --background: white;
  --hamburger-margin: 8px;
  --animation-timing: 200ms ease-in-out;
  --hamburger-height: calc(var(--bar-height) * 3 + var(--hamburger-gap) * 2);
}

.menu-wrapper {
  background-color: #0098db;
  border-radius: 4px;
  padding: 0.5rem 0.8rem 0.8rem;
  position: absolute;
  top: 1em;
  left: -75px;
  z-index: 100;
  display: flex;
  flex-direction: column;
}
.menu-wrapper > span {
  display: block;
  margin-bottom: 0.3rem;
  font-weight: 600;
  color: white;
}

.burger-wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--hamburger-gap);
  z-index: 2;
  cursor: pointer;
  width: 18px;
  margin: 0 auto;
  --x-width: calc(var(--hamburger-height) * 1.41421356237);
}
.burger-wrapper input {
  appearance: none;
  padding: 0;
  margin: 0;
  outline: none;
  pointer-events: none;
  opacity: 1;
}
.burger-wrapper input:checked {
  opacity: 0;
  width: 0;
}
.burger-wrapper input[type=checkbox]:focus, .burger-wrapper input[type=checkbox]:hover {
  border-color: unset;
  outline: none;
  border: none;
}

.burger-wrapper:has(input:focus-visible)::before,
.burger-wrapper:has(input:focus-visible)::after,
.burger-wrapper input:focus-visible {
  border: 1px solid var(--background);
  box-shadow: 0 0 0 1px var(--foreground);
}

.burger-wrapper::before,
.burger-wrapper::after,
.burger-wrapper input {
  top: 0;
  content: "";
  width: var(--bar-width);
  height: var(--bar-height);
  background-color: var(--foreground);
  color: var(--foreground);
  border-color: white;
  border-radius: 9999px;
  transform-origin: left center;
  transition: opacity var(--animation-timing), width var(--animation-timing), rotate var(--animation-timing), translate var(--animation-timing), background-color var(--animation-timing);
}

.burger-wrapper:has(input:checked) {
  --foreground: white;
  --background: #333;
}

.burger-wrapper:has(input:checked)::before {
  rotate: 45deg;
  width: var(--x-width);
  translate: 0 calc(var(--bar-height) / -2);
}

.burger-wrapper:has(input:checked)::after {
  rotate: -45deg;
  width: var(--x-width);
  translate: 0 calc(var(--bar-height) / 2);
}

