  width: 85%;
  padding-right: 5px;
}
body.path-search aside.layout-sidebar-first .block .facets-checkbox,
body.path-search aside.layout-sidebar-second .block .facets-checkbox {
  width: 1em;
}

.view-content mark {
  background-color: transparent;
  font-weight: bold;
  color: black;
}

@media print, screen and (min-width: 30em) {
  main form[action="/search/site"] .form-type-textfield,
  main .search.view .views-exposed-form .form-type-textfield {
    width: 75%;
  }
}

header form[action="/search/site"] .form-type-select,
header form[action="/search/site"] .form-type-textfield label {
  display: none;
}

div.block.block-facets {
  margin-top: 1em;
  margin-right: 1em;
}
div.block.block-facets h3 {
  margin-top: 0;
}
div.block.block-facets ul {
  padding-left: 0;
}
div.block.block-facets li {
  margin-left: 0;
  list-style-type: none;
}
div.block.block-facets li label {
  font-size: 0.7em;
}
div.block.block-facets input[type=checkbox] {
  width: 0.8em;
  height: 0.8em;
}

.search.view a {
  word-break: break-all;
}